<template>
  <div>
    <h1>식물 목록</h1>
    <!-- showKeywordPlantListYn : {{ showKeywordPlantListYn }} -->
    <div v-if="!showKeywordPlantListYn">
      <table>
        <thead>
          <tr>
            <th style="width:10%;">이름</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(plant,id) in plantList" :key="id" v-on:click="sendCrpId(plant)">
            <td class="td-letter">{{ plant.crp_nm }}</td>
          </tr>
        </tbody>
      </table>
    </div>
    <div v-else>
      <button v-on:click="setKeywordPlantListfalse();">전체보기</button>
      <table>
        <thead>
          <tr>
            <th style="width:10%;">이름</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(plant,id) in keywordPlantList" :key="id" v-on:click="sendCrpId(plant)">
            <td class="td-letter">{{ plant.crp_nm }}</td>
          </tr>
        </tbody>
      </table>
      
    </div>
  </div>
</template>

<script>

export default {
  props: ['plantList', 'keywordPlantList', 'showKeywordPlantListYn'], 
  components: {},
  data() {
    return {
    };
  },
  created() {
  },
  mounted() {
  },
  updated() {
  },
  methods: {
    sendCrpId(plantInfo) {
      this.$emit('plantClicked', plantInfo);
    },
    setKeywordPlantListfalse(){
      this.$emit('setAllPlantList');
    }
  },
};
</script>