<template>
  <div class="contents_div_4">
    <div class="contents_div_1">
      <PlantList  v-bind:plantList="dataPlantList" 
                        :keywordPlantList="dataKeywordPlantList"  
                        :showKeywordPlantListYn="keywordPlantListYn"
                  v-on:plantClicked="showPlantContent"
                  v-on:setAllPlantList="showAllPlantList">
      </PlantList>
    </div>
    <div class="contents_div_3">
      <PlantContent   class="contents_div_none" 
                      v-bind:plantInfo="paramPlantInfo">
      </PlantContent>
      <PlantClassification  class="contents_div_none" 
                            v-bind:plantInfo="paramPlantInfo" 
                            v-on:sendKeywordCropId="showKeywordPlantList">
      </PlantClassification>
      <PlantGuide class="contents_div_none" 
                  v-bind:plantInfo="paramPlantInfo">
      </PlantGuide>
    </div>
  </div>
</template>

<script>
import PlantList from './PlantList.vue';
import PlantContent from './PlantContent.vue';
import PlantClassification from './PlantClassification.vue';
import plantListJson from '../../assets/plantList.json'; // select get_result_json(17);
import PlantGuide from './PlantGuide.vue';


const dataPlantList = plantListJson;
const paramPlantInfo = {};
const dataKeywordPlantList = {};
const keywordPlantListYn = false;

export default {
  props: ['plantList'], 
  components: {
    PlantList,
    PlantContent,
    PlantClassification,
    PlantGuide,
  },
  data() {
    return {
      dataPlantList, 
      paramPlantInfo,
      dataKeywordPlantList,
      keywordPlantListYn,
    };
  },
  created() {

  },
  mounted() {

  },
  methods: {
    showPlantContent(plantInfo) {
      this.paramPlantInfo = plantInfo;
    },
    showKeywordPlantList(cropList) {
      // 선택한 키워드코드와 일치하는 작물 목록을 담는다.
      // console.log('Select Keyword Plant List : ', cropList);
      var idx = 0;
      // console.log('cropList.length : ', cropList.length);
      for(var i=0; i < cropList.length; i++) {
        // console.log('i = ', i);
        // console.log('cropList[i].crp_id = ', cropList[i]);
        for(var j=0; j < dataPlantList.length; j++) {
          // console.log('dataPlantList[j].crp_id = ', dataPlantList[j].crp_id);
          if (cropList[i] == dataPlantList[j].crp_id) {
            // console.log('일치 : dataPlantList[j] = ', dataPlantList[j]);
            this.dataKeywordPlantList[idx] = dataPlantList[j];
            idx++;
          }
        }
      }
      
      this.keywordPlantListYn = true;
    },
    showAllPlantList() {
      this.keywordPlantListYn = false;
    }
    
  },
};
</script>