<template>
    <div>
      <h1>{{ this.plantInfo.crp_nm }} 키워드</h1>
    <div v-for="(plantClass,id) in dataPlantClassification" :key="id">
        <div v-if="plantClass.crp_id == plantInfo.crp_id">
            <button v-on:click="sendKeywordCropId(plantClass.cdtyp_cd, plantClass.cd)">#{{ plantClass.cd_nm }}({{ plantClass.cd_expln }})</button> 
        </div>
    </div>

    </div>
    
  </template>
  
  <script>
import plantClassification from '../../assets/plantClassification.json'; // select get_result_json(26);
  
const dataPlantClassification = plantClassification;

export default {
    props: {
        plantInfo: Object,
    },
    components: {
    },
    data() {
      return {
        dataPlantClassification,
      };
    },
    created() {
    },
    mounted() {
    },
    updated() {
    },
    methods: {
        sendKeywordCropId(cdtypCd, cd) {
            // 선택한 키워드 관련 작물 id를 찾아서 상위 컴포넌트로 보낸다.
            var cropList = [];
            var i = 0;

            console.log(dataPlantClassification);
            for (i; i < dataPlantClassification.length; i++) {
                if(dataPlantClassification[i].cdtyp_cd == cdtypCd && dataPlantClassification[i].cd == cd) {
                    cropList.push(dataPlantClassification[i].crp_id)
                }
                console.log(dataPlantClassification[i].cdtyp_cd);
            }
            console.log('cropList ::: ', cropList);
            
            this.$emit('sendKeywordCropId', cropList);  // cropId
        }
    },
  };
  </script>